@import '../../themes/capsule.globals';

:host {
  display: block;

  font-family: $font-family-base;

  padding: 0px;
  margin: 0px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smooth: never;
  font-synthesis: none;
}

:host(.primary) {
  color: var(--color-override, var(--cpsl-color-text-primary));

  > p,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: var(--color-override, var(--cpsl-color-text-primary));
  }
}

