:host p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family-base;

  padding: 0px;
  margin: 0px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smooth: never;
  font-synthesis: none;

  &::-moz-selection {
    /* Code for Firefox */
    color: var(--cpsl-color-text-inverted) !important;
    background: var(--cpsl-color-background-64) !important;
  }

  &::selection {
    color: var(--cpsl-color-text-inverted) !important;
    background: var(--cpsl-color-background-64) !important;
  }
}
